import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Display from "./Display";
import { Container, Row, Col } from "react-bootstrap";
function home() {
  return (
    <div>
      <Container className="text-center p-4">
        <h1>Welcome to AI World</h1>
        <Row className=" ">
          <Col>
            <Display
              header="AI"
              title="Product"
              text="Lorenz"
              link="/product"
            />
          </Col>
          <Col>
            <Display
              header="AI"
              title="Products"
              text="Lorenz"
              link="/features"
            />
          </Col>
          <Col>
            <Display
              header="AI"
              title="Productsss"
              text="Lorenz"
              link="/pricing"
            />
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default home;
